.App {
}

.App-header {
  background-color: #222;
  padding: 20px;
  margin-bottom: 16px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.output-scrollWrapper {
  width: 640px;
  height: 400px;
  overflow: auto;
  resize: vertical;
  margin: 0 auto;
  padding: 0;
  text-align: left;
  background: #222222;
  color: #dddddd;
}
.output-linesContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  padding: 4px;
}
.output-line {
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  height: 15px;
  white-space: pre;
}
.output-lineno span {
  display: inline-block;
  width: 20px;
  margin-right: 16px;
  color: #5a5a5a;
  text-align: right;
}

.output-ansi:last-of-type {
  margin-right: 0px;
}
.output-prefix {
  margin-right: 4px;
}

.shell-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.shell-input {
  position: absolute;
  width: 0;
  height: 0;
  overflow: hidden;
  background: none;
  border: none;
  -webkit-appearance: none;
  outline: 0;
}

@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.shell-cursor {
  display: inline-block;
  background: none;
  border: 1px solid #dddddd;
  vertical-align: top;
  width: 8px;
  height: 15px;
  opacity: 0.5;
}
.shell-cursor.focused {
  opacity: 1;
  background: #dddddd;
  -webkit-animation: blink 1s step-end infinite;
          animation: blink 1s step-end infinite;
}



